export default {
	data () {
		return {
			bgImg: '../../static/image/activeArea.png',
			avatar:'../../static/image/ylc/11.png',
		}
	},
	methods:{
		back () {
			uni.navigateBack({
				delta: 1
			})
		},
		// 打开红包
		handleOpenRedBag(){
			uni.navigateTo({
				url:'../RandomRedBag/RandomRedBag'
			})
		},
		// 获取用户信息
		async getUserInfo(){
			let res = await this.$fetch(this.$api.getUserInfo,{},'GET')
			console.log(res);
			if(res.data.avatar){
				this.avatar  = res.data.avatar
			}
		}
	},
	onLoad() {
		this.getUserInfo()
	}
}
